import { defineCustomElement as defineCustomElementSkeletonText } from '@ionic/core/components/ion-skeleton-text';
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TP_ICONS_STORYBOOK } from '../../../../utils/constants/icon.constant';
import { TpIonIcon } from '../../../ionic/icon/utils/icon.type';
import { TpAlertFixed } from '../alert-fixed';
import { TpAlertFixedType } from '../utils/alert-fixed.type';

defineCustomElementSkeletonText();

const meta: Meta<TpAlertFixed & TpAlertFixedType & TpIonIcon> = {
  title: 'components/Core/Alert fixed',
  argTypes: {
    icon: {
      control: { type: 'select' },
      options: [...TP_ICONS_STORYBOOK],
      description: 'Define o icone.',
    },
    text: {
      control: 'text',
      description: 'Define o texto a ser exibido no componente.',
      table: {
        defaultValue: {
          summary: 'undefined',
        },
        type: {
          summary: 'string',
        },
      },
    },
  },
  args: {
    color: 'provas-4',
    icon: `${TP_ICONS_STORYBOOK[1]}`,
    text: 'Vers√£o desatualizada',
  },
};

export default meta;

type Story = StoryObj<TpAlertFixed & TpAlertFixedType & TpIonIcon>;

export const Desatualizado: Story = {
  render: ({ ...args }) => {
    const iconUrl = `./assets/${args.icon}`;

    return html`
      <tp-alert-fixed color="${args.color}">
        <ion-icon
          class="tp-alert-fixed__icon"
          slot="icon"
          tp-size="sm"
          src="${iconUrl}"
        ></ion-icon>
        <ion-text class="tp-alert-fixed__text" slot="text" tp-type="p12x">
          ${args.text}
        </ion-text>
      </tp-alert-fixed>
    `;
  },
};

export const Offline: Story = {
  render: ({ ...args }) => {
    const iconUrl = `./assets/${args.icon}`;

    return html`
      <tp-alert-fixed color="${args.color}">
        <ion-icon
          class="tp-alert-fixed__icon"
          slot="icon"
          tp-size="sm"
          src="${iconUrl}"
        ></ion-icon>
        <ion-text class="tp-alert-fixed__text" slot="text" tp-type="p12x">
          ${args.text}
        </ion-text>
      </tp-alert-fixed>
    `;
  },
};
